@use 'base/variables' as v;
@use 'base/mixins' as m;

.citas{
    list-style: none;
    padding: 0;
    li{
        padding: 1rem;
        border-top: 1px solid v.$blanco;
        p{
            color: v.$azul;
            font-weight: v.$bold;
        }
        span, .servicio{
            font-weight: v.$regular;
            color: v.$blanco;
        }
    }
}

.servicios{
    @extend .citas;

    .acciones-servicios{
        @include m.tablet{
            display: flex;
            justify-content: space-around;
            align-items: center;
        }
    }
}

