@use 'base/variables' as v;
@use 'base/mixins' as m;

.listado-servicios{
    @include m.tablet{
        @include m.grid(2,1rem);
    }
    .servicio{
        background-color: v.$azul;
        border-radius: 1rem;
        transition: all .3s ease;
        text-align: center;
        
        &.seleccionado{
            background-color: v.$blanco;
            p{
                color: v.$azul;
            }
        }
        .nombre-servicio, .precio-servicio{
            font-weight: v.$bold;
        }
        .precio-servicio{
            font-size: 2.5rem;
        }
        &:hover{
            cursor: pointer;
            transform: scale(1.1);
            border-radius: 1rem 0 1rem 0;
        }
        &:active{
            transform: scale(0.8);
        }
    }
}

