@use 'base/variables' as v;

.alerta{
    padding: calc(v.$separacion / 4);
    display: block;
    margin-bottom: 1rem;
    color: v.$blanco;
    width: 100%;
    text-align: center;
    font-weight: v.$bold;
    text-transform: uppercase;
    border-radius: .5rem;
    &.error{
        background-color: v.$rojo;
    }
    &.exito{
        background-color: v.$verde;
    }
}