@use 'base/variables' as v;
@use 'base/mixins' as m;

.formulario{
    margin-top: 5rem;
    width: 100%;
}
.campo{
    display: flex;
    margin-bottom: 2rem;
    align-items: center;
    label{
        flex: 0 0 10rem;
    }
    input{
        flex: 1;
        border: none;
        border-radius: 1rem;
        padding: calc(v.$separacion / 4);

        &:disabled{
            background-color: lighten(v.$negro, 40%);
            cursor: not-allowed;
            color: v.$blanco;
        }
    }
}

input[type="submit"]{
    border-radius: 1.5rem;
    transition: all 0.3s ease 0s;
    outline: none;
   
    &:hover {
        background-color: darken(v.$azul, 40%);
        color: #fff;
        transform: translateY(-.7rem);
   }
   
    &:active {
        transform: translateY(-.5rem);
    }
}