@use 'base/variables' as v;
@use 'base/mixins' as m;

.paginacion{
    padding: v.$separacion 0;

    @include m.telefono{
        display: flex;
        justify-content: space-between;
    }
    button{
        margin: .5rem;
        border-radius: 1rem;
        transition: all .1ms ease-in-out;

        &:hover{
            transform: translateY(-.5rem);
        }
    }

}

.ocultar{
    visibility: hidden;
}