@use 'base/variables' as v;
@use 'base/mixins' as m;

.contenido-resumen{
    p{
        margin-bottom: .5rem;
    }

    span{
        color: v.$azul;
        font-size: 2.4rem;
        font-weight: v.$bold;
    }

    h3, .titulo-servicio{
        font-style: normal;
        letter-spacing: 1.5px;
        text-transform: uppercase;
    
        span{
            letter-spacing: normal;
            text-transform: capitalize;
            color: v.$gris;
            font-size: 2rem;           
            
        }
    }
}

.contenedor-servicio{
    margin: 5.5rem 0;
    padding: 0rem 3rem;
    &::after{
        content: '';
        display: block;
        width: 95%;
        height: .5rem;
        background-color: v.$azul;
        border-radius: 1rem;
        margin: 0 auto;
    }

    .texto-servicio, .precio-servicio{
        text-align: center;
    }
    
}